

import Vue from 'vue'
import App from './App'
import router from './router'
import store from './vuex/store';

Vue.config.productionTip = false;

import VueScroller from 'vue-scroller';
Vue.use(VueScroller);


import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(ToastPlugin);
Vue.use(ConfirmPlugin);
Vue.use(LoadingPlugin);


import 'v-charts/lib/style.css';
import VeHistogram from 'v-charts/lib/histogram';
import VeLine from 'v-charts/lib/line';
import VePie from 'v-charts/lib/pie';
import 'echarts/lib/component/dataZoom';
import 'echarts/lib/component/title';
Vue.component(VeHistogram.name, VeHistogram);
Vue.component(VePie.name, VePie);
Vue.component(VeLine.name, VeLine);


import plugin from './public/js/plugin';
Vue.use(plugin);

router.beforeEach((to, from, next) => {

if (to.meta.requireAuth) {
if(sessionStorage.userId) {
next();
}else {
next({
path: '/',
query: {redirect: to.fullPath}  
})
}
}else {
next();
}
});

/* eslint-disable no-new */
new Vue({
el: '#app',
store,
router,
template: '<App/>',
components: { App }
})



